import {Component, Input, Output, EventEmitter} from '@angular/core';

@Component({
  selector: 'text-editor',
  templateUrl: './text-editor.component.html',
  styleUrls: [ './text-editor.component.css' ]
})
export class TextEditorComponent {
  @Input() fontSize: number = 14;
  @Output() fontSizeChange: EventEmitter<number> = new EventEmitter<number>();
  @Input() fontFamily: string = 'Lato';
  @Output() fontFamilyChange: EventEmitter<string> = new EventEmitter<string>();

  @Input() isBold: boolean;
  @Output() isBoldChange: EventEmitter<boolean> = new EventEmitter<boolean>();

  @Input() isItalic: boolean;
  @Output() isItalicChange: EventEmitter<boolean> = new EventEmitter<boolean>();

  @Input() isUnderline: boolean;
  @Output() isUnderlineChange: EventEmitter<boolean> = new EventEmitter<boolean>();

  @Input() isLinethrough: boolean;
  @Output() isLinethroughChange: EventEmitter<boolean> = new EventEmitter<boolean>();

  @Input() isOverline:boolean;

  @Output() isOverlineChange: EventEmitter<boolean> = new EventEmitter<boolean>();

  @Input() color: string;
  @Output() colorChange: EventEmitter<string> = new EventEmitter<string>();

  @Input() backgroundColor: string;
  @Output() backgroundColorChange: EventEmitter<string> = new EventEmitter<string>();

  @Input() opacity: number;
  @Output() opacityChange: EventEmitter<number> = new EventEmitter<number>();

  @Input() customText: string = 'Custom Text';
  @Output() customTextChange: EventEmitter<string> = new EventEmitter<string>();
  @Output('addTextEvent')
  addTextEvent = new EventEmitter();

  changeCustomText(event) {
    this.customTextChange.emit(this.customText);
  }

  changeFontFamily(event) {
    this.fontFamilyChange.emit(this.fontFamily);
  }

  changeFontSize(event) {
    this.fontSizeChange.emit(this.fontSize);
  }

  addText(event) {
    console.log('add text');
    this.addTextEvent.emit();
  }

  changeColor(event) {
    this.colorChange.emit(this.color);
    console.log('change color in text editor');
  }

  changeBackgroundColor(event) {
    this.backgroundColorChange.emit(this.backgroundColor);
  }

  toggleOverline(event) {
    this.isOverlineChange.emit(!this.isOverline);
  }

  changeOpacity (event) {
    this.opacityChange.emit(this.opacity);
  }

  toggleBold(event) {
    this.isBoldChange.emit(!this.isBold);
  }

  toggleItalic(event) {
    this.isItalicChange.emit(!this.isItalic);
  }

  changeIsUnderline($event) {
    this.isUnderlineChange.emit(!this.isUnderline);
  }

  toggleIsLineThrough($event) {
    this.isLinethroughChange.emit(!this.isLinethrough);
  }

}



