import { Injectable } from '@angular/core';

function getWindow (): any {
  return (<any>window).buyUrl;
}

function getbackgroundUrl():any {
  return (<any>window).backgroundUrl;
}

function getSaveImageUrl(): any {
  return (<any>window).saveImageUrl;

}

function getBuyUrl(): any {
  return (<any>window).buyUrl;

}

function getCartUrl(): any {
  return (<any>window).cartUrl;

}
function getProductId(): any {
  return (<any>window).productId;
}

function getCustomerId(): any {
  return (<any>window).customerId;
}

@Injectable()
export class WindowRefService {
  get nativeWindow (): any {
    return getWindow();
  }
  get saveUrl(): any {
    return getSaveImageUrl();
  }
  get buyUrl(): any {
    return getBuyUrl();
  }
  get backgroundUrl(): any {
    return getbackgroundUrl();
  }
  get productId(): any {
    return getProductId();
  }
  get customerId(): any {
    return getCustomerId();
  }
  get cartUrl(): any {
    return getCartUrl();
  }

  getGiftCardConfig() : any {
    return window;
  }
}



