import 'rxjs/add/operator/toPromise';
import { Headers, Http } from '@angular/http';
import { Art } from './art';
import { Giftcard } from './giftcard';
import { Injectable } from '@angular/core';
import {forEach} from "@angular/router/src/utils/collection";

function getArtFromWindow(): any {
 return (<any>window).arts;
}

function getBuyParamsFromWindow(): any {
  return (<any>window).params;

}
@Injectable()
export class ArtService {

  private headers = new Headers({'Content-Type': 'application/json'});
  private artUrl = 'api/heroes';  // URL to web api

  constructor(private http: Http) { }

  getGiftCardArt(): any {
    let out: Art[] =[];
    let arts: any = getArtFromWindow();
    let arrArray: any = arts.art;
    for (let item of arrArray)  {
      let stuff = new Art();
      stuff.title = item.title;
      stuff.id = item.id;
      stuff.name = item.name;
      stuff.src = item.src;
      out.push(stuff);
    }
    console.log(out);
    return out;
  }

  getAddToCartQty(): any {
    let params : any = getBuyParamsFromWindow();
    return params.qty;
  }
  /**
   * get the add to cart params that customer already added on product pages
   */
  getAddToCartParams(): any {

    let params: any = getBuyParamsFromWindow();
    let item: any = params.giftcard;
    let stuff = new Giftcard();
    stuff.recipient_name = item.giftcard_recipient_name;
    stuff.recipient_email = item.giftcard_recipient_email;
    stuff.sender_name = item.giftcard_sender_name;
    stuff.sender_email = item.giftcard_sender_email;

    stuff.headline = item.giftcard_headline;
    stuff.message = item.giftcard_message;
    stuff.schedule_send_date = item.giftcard_schedule_send_time;
    stuff.giftcard_amount = item.giftcard_amount;
    return stuff;
  }

  getArts(): Promise<Art[]> {
    return this.http.get(this.artUrl)
      .toPromise()
      .then(response => response.json().data as Art[])
      .catch(this.handleError);
  }


  getHero(id: number): Promise<Art> {
    const url = `${this.artUrl}/${id}`;
    return this.http.get(url)
      .toPromise()
      .then(response => response.json().data as Art)
      .catch(this.handleError);
  }

  private handleError(error: any): Promise<any> {
    console.error('An error occurred', error); // for demo purposes only
    return Promise.reject(error.message || error);
  }
}



