import {Component, OnInit, ElementRef, Input, Output, EventEmitter} from '@angular/core';

import {Art} from './art';

import  {ArtService} from './art.service';

@Component({
  selector: 'art',
  templateUrl: './art.component.html',
  styleUrls: [ './art.component.css' ]
})
export class ArtComponent implements OnInit {

  arts: Art[] = [];

  activeImg:any = null;

  @Output('sendImgEvent')
  sendImgEvent = new EventEmitter<string>();

  @Output('addImgEvent')
  addImgEvent = new EventEmitter();


  constructor (private artService: ArtService) { }

  ngOnInit(): void {
    this.arts = this.artService.getGiftCardArt();
  }

  openPanel() {
    console.log('onpen Panel');
  }

  extractText( str ){
  let ret = "";

  if ( /"/.test( str ) ){
    ret = str.match( /"(.*?)"/ )[1];
  } else {
    ret = str;
  }

  return ret;
}
  selectImg(event) {
    console.log('Click Image');
    let imgscr = event.target.attributes.src;
    let imgUrl =imgscr.value;
    console.log(imgUrl);
    this.activeImg = imgUrl;
    this.sendImgEvent.emit(this.activeImg );
  }
  addImg(event) {
    this.addImgEvent.emit();
  }
}



